.songMenu .cover {
    position: relative;
    width: 100%;
    height: 300px;
    background-color: rgb(221, 221, 221);
}

.songMenu {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    background-color: #fff;
    transition: all .5s;
    transform-origin: top right;
    transform: rotate(-90deg);
}

.songMenu .cover .bg {
    width: 100%;
    height: 105%;
    background-size: 100% 105%;
    filter: blur(8px);
}

.songMenu .cover .content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.songMenu .cover .content .am-navbar-light {
    color: #eee;
    background: rgba(0, 0, 0, 0);
}

.songMenu .cover .content .am-navbar  .title {
    width: 80%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.songMenu .cover .content .am-navbar-left {
    flex: 100;
}

.songMenu .cover .content .synopsis {
    display: flex;
    margin: 20px 20px 0;
}
.songMenu .cover .content .synopsis div{
    flex: 1;
    overflow: hidden;
    color: #eee;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.songMenu .cover .content .synopsis div .title{
    width: 95%;
    overflow: hidden;
    font-size: 16px;
    font-weight: bold;
}

.songMenu .cover .content img {
    width: 110px;
    height: 110px;
    border-radius: 5px;
    margin-right: 10px;
}

.songMenu .cover .content div {
    line-height: 50px;
}


.songMenu .cover .content div .text {
    display: flex;
    align-items: center;
    color: #bba9a9;
}

.songMenu .cover .content div .text img {
    width: 16px;
    height: 16px;
    border-radius: 8px;
    margin-right: 3px;
}

.songMenu .cover .content .praise ul {
    display: flex;
    margin-top: 20px;
}

.songMenu .cover .content .praise ul li {
    height: 80px;
    flex: 1;
    text-align: center;
    color: #dadfe6;
    line-height: 1;
}

.songMenu .cover .content .praise ul li i.iconfont {
    display: block;
    font-size: 22px;
    margin-bottom: 10px;
}

.songMenu .songMenuList {
    position: absolute;
    top: 45px;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.songMenu .songMenuList .am-list {
    padding-top: 250px;
    padding-bottom: 105px;
}

.songMenu .songMenuList .am-list .am-list-body{
    overflow: hidden;
    border-radius: 6px 6px 0 0;
}

.songMenu .songMenuList .title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 45px;
    color: #333;
    border-bottom: 1px solid #f1f1f1;
    margin-left: 10px;
}

.songMenu .songMenuList .title i.icon-bofang {
    font-size: 24px;
    margin: 0 10px;
}

.songMenu .songMenuList .title span {
    font-size: 16px;
}

.songMenu .songMenuList .title span span {
    font-size: 12px;
    color: #bba9a9;
}

.songMenu .songMenuList .box {
    display: flex;
    align-items: center;
    padding-right: 10px;
}

.songMenu .songMenuList .box .index {
    text-align: center;
    width: 60px;
}

.songMenu .songMenuList .box .am-list-item {
    padding-left: 0;
    flex: 1;
}

.songMenu .songMenuList .box .am-list-item .am-list-line {
    padding: 5px;
}

.songMenu .songMenuList .box .am-list-item .am-list-line .am-list-brief {
    font-size: 12px;
}

.songMenu .songMenuList .title .am-button {
    height: 100%;
    line-height: 45px;
    background-color: #d4473e;
    color: #f0f0f1;
    padding: 0 10px;
}

.songMenu .songMenuList .title .am-button .num {
    font-size: 14px;
}

.songMenu .am-icon-loading {
    position: fixed;
    width: 40px;
    height: 40px;
    left: 50%;
    margin-left: -20px;
    top: 70%;
}

